<template>
  <div class="web_home">
      <div class="web-home-top">
        <el-row>
          <el-col :span="5" class="text_right" style="padding-top: 10px;">
            <img src="../../static/img/hx.png"/>
          </el-col>
          <el-col :offset="3" :span="16">
            <el-menu  :default-active="activeIndex"  class="el-menu-demo"  mode="horizontal"  @select="handleSelect"  background-color="#2f3f61"  text-color="#909399"  active-text-color="#ffffff">
              <el-menu-item  index="1">{{$t("home.whatishx")}}</el-menu-item>
              <el-menu-item   index="2">{{$t("home.ecoco")}}</el-menu-item>
              <!-- <el-menu-item index="3">{{$t("home.comself")}}</el-menu-item> -->
              <el-submenu index="4" class="submenu_select">
                <template   slot="title">{{$t("home.userguide")}}</template>
                <el-menu-item   index="2-1">{{$t("home.miningguide")}}</el-menu-item>
                <el-menu-item   index="2-2">{{$t("home.offdoc")}}</el-menu-item>
                <el-menu-item   index="2-3">{{$t("home.walletdownload")}}</el-menu-item>
                <el-menu-item   index="2-4">{{$t("home.comself")}}</el-menu-item>
              </el-submenu>
              <el-menu-item   index="5">{{$t("home.explore")}}</el-menu-item>
              <el-submenu index="6" class="submenu_select">
                <template   slot="title">{{$t("home.news")}}</template>
                <el-menu-item   index="6-1">{{$t("home.videointro")}}</el-menu-item>
                <el-menu-item   index="6-2">{{$t("home.annocenter")}}</el-menu-item>
                <el-menu-item   index="6-3">Blog</el-menu-item>
                <el-menu-item   index="6-4">{{$t("home.contactus")}}</el-menu-item>
              </el-submenu>
              <el-submenu index="7" class="submenu_select" >
                <template   slot="title">{{$t("home.language")}}</template>
                <el-menu-item   index="7-1">中文</el-menu-item>
                <el-menu-item   index="7-2">English</el-menu-item>
              </el-submenu>
            </el-menu>
          </el-col>
        </el-row>
      </div>
      <div :class="key == '2-1'?'home_main':'home_main_hidden'">
        <!-- <transition name="slide-fade"> -->
        <router-view :webHomeParam="webHomeParam" />
        <!-- </transition> -->
      </div>
      <!-- <el-main v-bind:style="{height: height+'px'}">
        <div class="home_main_img">
          <img src="../../../static/img/main.png" class="main_img">
          <div class="words">
            世界是相连的，区块链也是如此<br/>HyperExchange，真正意义上的第一个跨链项目
          </div>
          <div class="more">
            <el-button round>Learn More&ensp;&ensp;→</el-button>
          </div>
        </div>
      </el-main>
      <el-footer>
        <el-row>
          <el-col :span="4" style="padding-top: 6pt;text-align:right;">
            <img class="notice" src="../../../static/img/notice.png"/>
          </el-col>
          <el-col :span="16" class="text_notice" style="text-align:left;padding-left: 20px;">这是一个公告！这是一个公告！这是一个公告！
          </el-col>
          <el-col :span="4" class="text_notice" style="text-align:left;"><i class="el-icon-circle-close-outline" @click="iconClick()"></i>
          </el-col>
        </el-row>
      </el-footer>
    </el-container> -->
  </div>
</template>
<script>
export default {
  name: 'WebHome',
  data () {
    return {
      activeIndex: '1',
      webHomeParam: {
        activeIndexParam: 0,
        isHome: false,
        isZh: ''
      },
      key: '',
      theme: 'dark'
    }
  },
  watch: {
    $route () {
      const routerPath = this.$route.path
      let index = routerPath.includes('mining') || routerPath.includes('ecosystem') || routerPath.includes('download') || routerPath.includes('summary')
      if (index === -1 || index === false) {
        this.key = ''
      } else {
        this.key = '2-1'
      }
    }
  },
  mounted () {
    const routerPath = this.$route.path
    let index = routerPath.includes('mining') || routerPath.includes('ecosystem') || routerPath.includes('download') || routerPath.includes('summary')
    console.log(index)
    if (index === -1 || index === false) {
      this.key = ''
    } else {
      this.key = '2-1'
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      this.key = key
      this.webHomeParam.isHome = false
      this.webHomeParam.activeIndexParam = 0
      if (key === '7-1') {
        localStorage.setItem('lang', 'zh')
        this.$i18n.locale = 'zh'
        this.webHomeParam.isZh = true
        this.key = '2-1'
      } else if (key === '7-2') {
        localStorage.setItem('lang', 'en')
        this.$i18n.locale = 'en'
        this.webHomeParam.isZh = false
        this.key = '2-1'
      } else if (key === '1') {
        this.$router.push({ path: '/home/home' })
        setTimeout(() => {
          this.webHomeParam.isHome = true
        }, 100)
      } else if (key === '2') {
        this.$router.push({ path: '/home/ecosystem' })
      } else if (key === '2-1') {
        this.$router.push({ path: '/home/mining' })
      } else if (key === '2-3') {
        this.$router.push({ path: '/home/download' })
      } else if (key === '6-1') {
        this.$router.push({ path: '/home/video' })
      } else if (key === '6-2') {
        this.$router.push({ path: '/home/anno' })
      } else if (key === '6-3') {
        window.open('https://medium.com/@hyperexchange', '_blank')
      } else if (key === '6-4') {
        this.$router.push({ path: '/home/home' })
        setTimeout(() => {
          this.webHomeParam.activeIndexParam = 5
        }, 100)
      } else if (key === '2-4') {
        window.open('http://senator.hx.cash/', '_blank')
      } else if (key === '5') {
        window.open('http://explorer.hx.cash/', '_blank')
      } else if (key === '2-2') {
        window.open('http://docs.hx.cash/', '_blank')
      }
    },
    iconClick () {
      console.log(11111111)
    }
  }
}
</script>
<style scoped>
.web_home {
  position: absolute;
  left:0px;
  top:0px;
  right: 0px;
  bottom: 0px;
}
.web-home-top {
  background: #2f3f61;
  line-height: 80px;
  height: 80px;
  width: 100%;
}

.web-home-top .el-col {
  height: 80px;
}
.el-menu--horizontal {
  border-bottom: #e6e6e6;
}
.text_right {
  text-align: right;
}
.home_main {
  width: 100%;
  height: calc(100% - 80px);
  transition: left 0.42s;
}
.home_main_hidden {
  overflow-y: hidden;
  width: 100%;
  height: calc(100% - 80px);
  transition: left 0.42s;
}
.slide-fade{
 position: absolute;
 top:0;
 bottom: 0;
}
.slide-fade-enter-active {
 transition: all 2s ease;
}
.slide-fade-leave-active {

 transition: all .1s cubic-bezier(2.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
{
 top:0;
 bottom: 0;
 transform: translateY(50px);
 opacity: 0;
}
</style>
<style>
[] {
    display: none !important;
}
.el-submenu .el-menu--horizontal {
  top: 80px !important;
}
.el-menu--collapse .el-menu .el-submenu, .el-menu--popup {
    min-width: 100px !important;
}
/* .web_home .el-menu--collapse .el-menu .el-submenu, .web_home .el-menu--popup {
  width: 100px !important;
} */
.web_home .el-menu--horizontal>.el-submenu .el-submenu__title {
  height: 80px;
  line-height: 80px;
  font-size: 18px;
}
.web_home .el-menu--horizontal>.el-menu-item {
  height: 80px;
  line-height: 80px;
  font-size: 18px;
}
</style>



